body{
    margin: 0 auto;
    border: 0;
    background-color: black;
    color: white;
    font-size: 1.3rem;
}
a {
    text-decoration:none;
    color: white;
}
ul, li{
    padding: 0;
    list-style: none;
}
span, .sub-nav {
    padding: 0 30px 0 30px;
    font-size: 1.2rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.8);
}
.logo{
    color: red;
    font-size: 3.2rem;
    font-weight: 600;
}
.nav{
    width: 50%;
}
.icon{
    padding: 0 1rem;
}
header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 75px;
    padding: 1rem 5% 1rem 5%;
    font-weight: bold;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 3000;
  }
.hole{
    padding: 3rem;
}
.for-video{
    position: relative;
}
.video-text{
    position: absolute;
    top: 65%;
    left: 3%;
    font-size: 3.2rem;
} 
.main-title{
    padding: 0px 100px 100px 100px;
    text-align: center;
    font-size: 50px;
    font-weight: bold;
}
.genre{
    font-size: 15px;
}
.title {
    padding: 15px;
    font-size: 25px;
    text-align: center;
}

/* grid 이용해서 한 번에 4개 보여주기 - Home.js*/
.main{
    overflow-x: scroll;
    display: grid;
    grid-template-columns: repeat(20, 1fr);
    place-items: center;
    padding: 50px 30px 50px 30px;
    column-gap: 30px;
}
.main::-webkit-scrollbar {
    width: 1px;  /* 스크롤바의 너비 */
}
.main::-webkit-scrollbar-thumb {
    height: 10%;
    background: #ffffff2a; /* 스크롤바의 색상 */
    border-radius: 10px;
}
/* 안의 내용물 가운데정렬  - Movie.js*/
.main-text{
    justify-content: center;
    align-items: center;
    text-align: center;
}
/* hover 시 */
.main-text:hover{
    transform: scale( 1.1, 1.1 );
    transition: transform .3s;
}
.sub-title{
    font-size: 1.5rem;
    padding: 2.5rem 0 0 0;
}